import Vue from "vue";
import Router from "vue-router";
import { getMenu } from "req/api/menu";
import store from "store";
import * as types from "store/mutation_types";
// import {mapActions} from 'vuex'

const home = () => import("views/home/home");
const login = () => import("views/user/login");
const install = () => import("views/install");
const error404 = () => import("views/status/404");
const unauthorized = () => import("views/status/unauthorized");
/* 资产管理 */
const assets = () => import("views/assets");
const assets_list = () => import("views/assets/list");
const assets_area = () => import("views/assets/area");
const assets_business = () => import("views/assets/business");
const assets_facilitator = () => import("views/assets/facilitator");
const assets_organization = () => import("views/assets/organization");
/* 主机服务管理 */
const hostservice = () => import("views/hostservice");
const hostservice_disguise = () => import("views/hostservice/disguise");
const hostservice_netfilter = () => import("views/hostservice/netfilter");
const hostservice_ips = () => import("views/hostservice/ips");
const hostservice_cfgcheck = () => import("views/hostservice/cfgcheck");
const hostservice_extortion = () => import("views/hostservice/extortion");
const hostservice_firewallrule = () => import("views/hostservice/firewallrule");
/* 日志管理 */
const system_log = () => import("views/system/log");
const system_log_operationlog = () => import("views/system/log/operationlog");
const system_log_refreshlog = () => import("views/system/log/refreshlog");
const system_log_communicationlog = () =>
  import("views/system/log/communicationlog");
  /* 中间件管理 */
const system_middleware = () => import("views/system/middleware");
const system_middleware_logmanage = () =>
  import("views/system/middleware/logmanage");
const system_middleware_modulemanage = () =>
  import("views/system/middleware/modulemanage");
const system_middleware_proxymanage = () =>
  import("views/system/middleware/proxymanage");
/* 系统设置 */
const system = () => import("views/system");
const system_authorization = () => import("views/system/authorization");
const system_database = () => import("views/system/database");
const system_database_back = () => import("views/system/database/back");
const system_database_recovery = () => import("views/system/database/recovery");
const system_database_del = () => import("views/system/database/del");
const system_managehost = () => import("views/system/managehost");
const system_syncset = () => import("views/system/syncset");
const system_syncset_syslog = () => import("views/system/syncset/syslog");
const system_syncset_elk = () => import("views/system/syncset/elk");
const system_platformset = () => import("views/system/platformset");
const system_productupdate = () => import("views/system/productupdate");
const system_productupdate_online = () =>
  import("views/system/productupdate/online");
const system_productupdate_offline = () =>
  import("views/system/productupdate/offline");
const system_usermanagement = () => import("views/system/usermanagement");

Vue.use(Router);

let router = new Router({
  routes: [
    {
      path: "/",
      redirect: "/home",
      meta: {
        requireAuth: true,
        name: "主页"
      }
    },
    {
      path: "/home",
      name: "home",
      component: home,
      meta: {
        requireAuth: true,
        name: "主页"
      }
    },
    {
      path: "/login",
      name: "login",
      component: login,
      meta: {
        name: "登录"
      }
    },
    {
      path: "/install",
      name: "install",
      component: install,
      meta: {
        name: "产品安装向导"
      }
    },
    {
      path: "/404",
      name: "404",
      component: error404,
      meta: {
        requireAuth: true,
        name: "404"
      }
    },
    {
      path: "/unauthorized",
      name: "unauthorized",
      component: unauthorized,
      meta: {
        name: "温馨提示",
        requireAuth: true,
        layer: true,
        area: ["350px", "250px"],
        component: "unauthorized"
      }
    }
  ]
});

//
