<template>
  <div class="database-back-wrap">
    <div class="back-wrap">
      <i class="el-icon-question"></i>
      <span>{{msg}}</span>
    </div>
    <div class="customer-button-wrap">
      <el-button size="small" class="cancel">取消</el-button>
      <el-button type="primary" size="small" class="onSubmit">确定</el-button>
    </div>
  </div>
</template>

<script>
import { backDataBase } from "req/api/system/database";
export default {
  name: "database_back_wrap",
  data() {
    return {
      msg: "确定备份数据库吗?"
    };
  },
  props: {
    layerid: {
      type: String
    },
    setCnavPath: {
      type: Function
    }
  },
  methods: {
    cancel() {
      this.setCnavPath();
      this.$layer.close(this.layerid);
    },
    onSubmit() {
      this.__backDataBase()
    },
    __backDataBase() {
      backDataBase().then(res => {
        this.$message({
          type: "success",
          message: "备份成功"
        });
        this.setCnavPath();
        this.$layer.close(this.layerid);
      });
    }
  }
};
</script>

<style scoped lang="less">
.database-back-wrap {
  width: 100%;
  height: 100%;
  position: relative;
  .back-wrap {
    padding: 20px 15px;
    i {
      font-size: 30px;
      vertical-align: middle;
      color: #f5dd35;
    }
    span {
      font-size: 14px;
      color: #666;
    }
  }
  .customer-button-wrap {
    position: absolute;
    bottom: 10px;
    right: 20px;
  }
}
</style>





