import { ajaxPro } from "req/http/http";

export function backDataBase() {
  return ajaxPro({
    url: "/database/backup",
    type: "post"
  })
    .then(res => {
      return Promise.resolve(res);
    })
    .catch(err => {
      return Promise.reject(err);
    });
}

export function clearDataBase() {
  return ajaxPro({
    url: "/database/clear",
    type: "post"
  })
    .then(res => {
      return Promise.resolve(res);
    })
    .catch(err => {
      return Promise.reject(err);
    });
}

export function resetDataBase(param) {
  return ajaxPro({
    url: "/database/reset",
    data: param,
    type: "post"
  })
    .then(res => {
      return Promise.resolve(res);
    })
    .catch(err => {
      return Promise.reject(err);
    });
}



