<template>
  <div class="system-syncset-elk">
    <mu-form
      :model="formData"
      class="mu-demo-form"
      :label-position="labelPosition"
      label-width="100"
      ref="system_syncset_elk"
    >
      <mu-form-item prop="hosts" label="配置地址" :rules="hostsRules">
        <mu-text-field
            v-model="formData.hosts"
            :rows-min="3"
            multiLine
            :rows-max="5"
            :max-length="500"
          />
      </mu-form-item>
      <mu-form-item prop="user" label="用户名" :rules="userRules">
        <mu-text-field v-model="formData.user" :max-length="50"></mu-text-field>
      </mu-form-item>
      <mu-form-item prop="password" label="密码" :rules="passwordRules">
        <mu-text-field v-model="formData.password"
        :action-icon="visibility ? 'visibility_off' : 'visibility'"
          :action-click="() => (visibility = !visibility)"
          :type="visibility ? 'text' : 'password'"
         :max-length="50"></mu-text-field>
      </mu-form-item>
      <mu-form-item prop="index" label="数据库名" :rules="indexRules">
        <mu-text-field v-model="formData.index" :max-length="50"></mu-text-field>
      </mu-form-item>
      <mu-form-item prop="status" label="状态">
        <mu-radio
          v-for="item in dropDownData.status"
          :key="item.id"
          v-model="formData.status"
          :value="item.id"
          :label="item.name"
        ></mu-radio>
      </mu-form-item>
      <mu-form-item>
        <mu-button color="primary" small @click="submitEdit">确定</mu-button>
        <mu-button @click="cancel" small>取消</mu-button>
      </mu-form-item>
    </mu-form>
  </div>
</template>
<script>
import { getConfig, setConfig } from "req/api/serverconfig/configset";
export default {
  name: "system_syncset_elk",
  data() {
    return {
      labelPosition: "right",
      visibility:true,
      hostsRules: [
        { validate: val => !!val, message: "配置地址不能为空" },
        {
          validate: val => this.$validates.checkMoreIPandPort(val),
          message: "配置地址为ip加端口号,可用逗号隔开,例如:192.0.1.1:80,192.168.0.1:60"
        },
        {
          validate: val => {
            let array = val ? val.toString().split(",") : [];
            return !this.$validates.isSelfRepeat(array)
          },
          message: "配置地址重复"
        }
      ],
      userRules: [{ validate: val => !!val, message: "用户名不能为空" }],
      indexRules: [{ validate: val => !!val, message: "数据库名称不能为空" }],
      passwordRules: [{ validate: val => !!val, message: "密码不能为空" }],
      formData: {
        hosts: "",
        user: "",
        password: "",
        index: "",
        status: true,
        type: "elk"
      },
      dropDownData: {
        status: [
          {
            id: true,
            name: "开启"
          },
          {
            id: false,
            name: "关闭"
          }
        ]
      }
    };
  },
  props: {
    layerid: {
      type: String
    }
  },
  created() {
    this.__getConfig({
      type:"elk"
    })
  },
  methods: {
    submitEdit() {
      this.$refs.system_syncset_elk.validate().then(result => {
        if (result) {
          this.$isShowTip(
            "system",
            "system_syncset",
            "确定提交吗?",
            () => {
              this.__setConfig(this.formData);
            },
            this
          );
        }
      });
    },
    cancel() {
      this.$isShowTip(
        "system",
        "system_syncset",
        "确定取消吗？",
        () => {
          this.$layer.close(this.layerid);
        },
        this
      );
    },
    __getConfig(param) {
      getConfig(param)
        .then(res => {
          this.formData.hosts = res.data.hosts;
          this.formData.user = res.data.user;
          this.formData.index = res.data.index;
          this.formData.password = res.data.password;
          this.formData.status = res.data.status;
        })
        .catch(err => {});
    },
    __setConfig(param) {
      setConfig(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "提交成功"
          });
          this.$layer.close(this.layerid);
        })
        .catch(err => {});
    }
  }
};
</script>
<style scoped>
.system-syncset-elk {
  width: 100%;
  padding: 10px 20px 0 0;
}
</style>





