<template>
  <div class="productupdate-online-wrap">
    <div class="online-wrap">
      <i class="el-icon-question"></i>
      <span>{{msg}}</span>
    </div>
    <div class="customer-button-wrap">
      <el-button size="small" @click="cancel">取消</el-button>
      <el-button type="primary" size="small" @click="onSubmit">确定</el-button>
    </div>
  </div>
</template>

<script>
import { onlineUpdate } from "req/api/system/productupdate";
export default {
  name: "productupdate_online_wrap",
  data() {
    return {
      msg: "确定更新产品吗?"
    };
  },
  props: {
    layerid: {
      type: String
    },
    setCnavPath: {
      type: Function
    }
  },
  methods: {
    cancel() {
      this.setCnavPath();
      this.$layer.close(this.layerid);
    },
    onSubmit() {
      this.__onlineUpdate()
    },
    __onlineUpdate(param) {
      onlineUpdate(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "系统正在更新,请稍等..."
          });
          this.setCnavPath();
          this.$layer.close(this.layerid);
        })
        .catch(err => {});
    }
  }
};
</script>

<style scoped lang="less">
.productupdate-online-wrap {
  width: 100%;
  height: 100%;
  position: relative;
  .online-wrap {
    padding: 20px 15px;
    i {
      font-size: 30px;
      vertical-align: middle;
      color: #f5dd35;
    }
    span {
      font-size: 14px;
      color: #666;
    }
  }
  .customer-button-wrap {
    position: absolute;
    bottom: 10px;
    right: 20px;
  }
}
</style>





