import { ajaxPro } from "req/http/http";

export function addUser(param) {
  return ajaxPro({
    url: "/user/add",
    data: param,
    type: "post"
  })
    .then(res => {
      return Promise.resolve(res);
    })
    .catch(err => {
      return Promise.reject(err);
    });
}

