<template>
  <div class="system-syncset-syslog">
    <mu-form
      :model="formData"
      class="mu-demo-form"
      :label-position="labelPosition"
      label-width="100"
      ref="system_syncset_syslog"
    >
      <mu-form-item prop="host" label="IP地址" :rules="hostRules">
        <mu-text-field v-model="formData.host" :max-length="50"></mu-text-field>
      </mu-form-item>
      <mu-form-item prop="port" label="端口号" :rules="portRules">
        <mu-text-field v-model="formData.port" :max-length="50"></mu-text-field>
      </mu-form-item>
      <mu-form-item prop="protocol" label="协议">
       <mu-select v-model="formData.protocol">
        <mu-option v-for="item in dropDownData.protocol" :key="item.id" :label="item.name" :value="item.id"></mu-option>
      </mu-select>
      </mu-form-item>
      <mu-form-item prop="status" label="状态">
        <mu-radio
          v-for="item in dropDownData.status"
          :key="item.id"
          v-model="formData.status"
          :value="item.id"
          :label="item.name"
        ></mu-radio>
      </mu-form-item>
      <mu-form-item>
        <mu-button color="primary" small @click="submitEdit">确定</mu-button>
        <mu-button @click="cancel" small>取消</mu-button>
      </mu-form-item>
    </mu-form>
  </div>
</template>
<script>
import { getConfig, setConfig } from "req/api/serverconfig/configset";
export default {
  name: "system_syncset_syslog",
  data() {
    return {
      labelPosition: "right",
      hostRules: [
        { validate: val => !!val, message: "IP不能为空" },
        {
          validate: val => this.$validates.ipReg.test(val),
          message: "请输入正确的IP地址,例如:1.1.1.1"
        }
      ],
      portRules: [
        { validate: val => !!val, message: "端口号不能为空" },
        {
          validate: val => !isNaN(val) && parseInt(val) > 0 && parseInt(val) < 65536 && /^[^0]/.test(val),
          message: "请输入正确的端口号,端口号大于0小于65536"
        }
      ],
      protocolRules: [
        { validate: val => !!val, message: "协议不能为空" }
      ],
      formData: {
        host: "",
        port: "",
        status: 1,
        protocol:"",
        type:"syslog"
      },
      dropDownData: {
        status: [
          {
            id: true,
            name: "开启"
          },
          {
            id: false,
            name: "关闭"
          }
        ],
        protocol: [
          {
            id: "tcp",
            name: "TCP"
          },
          {
            id: "udp",
            name: "UDP"
          }
        ]
      }
    };
  },
  props: {
    layerid: {
      type: String
    }
  },
  created() {
      this.__getConfig({
          type:"syslog"
      })
  },
  methods: {
    submitEdit() {
      this.$refs.system_syncset_syslog.validate().then(result => {
        if (result) {
          this.$isShowTip(
            "system",
            "system_syncset",
            "确定提交吗?",
            () => {
              this.__setConfig(this.formData);
            },
            this
          );
        }
      });
    },
    cancel() {
      this.$isShowTip(
        "system",
        "system_syncset",
        "确定取消吗？",
        () => {
          this.$layer.close(this.layerid);
        },
        this
      );
    },
    __getConfig(param) {
      getConfig(param)
        .then(res => {
          this.formData.host = res.data.host;
          this.formData.port = res.data.port;
          this.formData.protocol = res.data.protocol;
          this.formData.status = res.data.status || false;
        })
        .catch(err => {});
    },
    __setConfig(param) {
      setConfig(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "提交成功"
          });
          this.$layer.close(this.layerid);
        })
        .catch(err => {});
    }
  }
};
</script>
<style scoped>
.system-syncset-syslog {
  width: 100%;
  padding: 10px 20px 0 0;
}
</style>





