import { ajaxPro } from "req/http/http";

export function offlineUpdate(params) {
  return ajaxPro({
    url: "/update/offline",
    type: "post",
    contentType: false,
    processData: false,
    data: params
  })
    .then(res => {
      return Promise.resolve(res);
    })
    .catch(err => {
      return Promise.reject(err);
    });
}

export function onlineUpdate(params) {
  return ajaxPro({
    url: "/update/online",
    type: "post",
    data:params
  })
    .then(res => {
      return Promise.resolve(res);
    })
    .catch(err => {
      return Promise.reject(err);
    });
}



